.main-about{
    padding: 50px 0 100px;
    .container{
        display: flex;
        flex-wrap: wrap;
    }
    .zag{
        font-size: 36px;
        line-height: 50px;
        text-align: center;
        color: #232323;
        max-width: 1200px;
        margin: 0 auto 50px;
        &__red{
            font-weight: normal;
            color: #F74B4B;
            font-size: 20px;
            line-height: 120%;
            display: block;
            margin-bottom: 13px;
        }
    }
    &__ul{
        max-width: 430px;
        width: 100%;
        margin-left: 60px;
        li{
            padding-left: 80px;
            padding-top: 5px;
            background-position: left top;
            margin-bottom: 35px;
        }
        .name{
            font-weight: 600;
            font-size: 18px;
            line-height: 120%;
            color: #F74B4B;
            margin-bottom: 14px;
            display: block;
        }
        p{
            font-size: 16px;
            line-height: 150%;
        }
    }
    &__img{
        width: calc(100% - 490px);
        background-size: contain;
        background-position: center;
        height: auto;
        background-repeat: no-repeat;
    }
}


.about-in-page{
    .main-about__ul{
        width: 100%;
        max-width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-left: 0;
        li{
            width: 32%;
        }
    }
}
.mfp-with-zoom{
    .mfp-img{
        margin: 0;
        padding: 0;
        background-color: transparent;
        padding-bottom: 40px;
    }
}
